// Curl Shadow
// <div class="curlshadow">
//   {{ content }}
// </div>
.curlshadow {
  @include curlshadow(0.1);
}


// Sidebox
// <div class="sidebox">
//   {{ content }}
// </div>
.sidebox {
  @include shine(0.7, -15px);
  background-image: radial-gradient(50% 50%, #c5c1af 0px, #b6b2a2 100px);
  border-bottom: 1px solid rgba(0,0,0,0.2);
  border-radius: 5px;
  padding: 15px 10px;
  position: relative;
  z-index: 0;
  p {
    color: #424036;
    font-size: 14px;
    line-height: 1.6em;
  }
  &.project {
    margin-top: 15px;
    .description, h3 {
      margin: 0px 0px 0px 50px;
    }
    .description {
      margin-top: 5px;
      font-size: 13px;
    }
    [class*='icomoon-'] {
      float: left;
      font-size: 40px;
      margin-right: 0px 0px 0px 4px;
    }
  }
}

// Thumbnails
// <div class="thumbright">
//   <img src="{{imgpath}}"/>
// </div>
@mixin thumb() {
  @include shine(0.4);
  background-image: linear-gradient(#555, #333);
  border-radius: 3px;
  float: right;
  margin: 0px 0px 5px 5px;
  padding: 3px 4px 4px 4px;
  position: relative;
  z-index: 1;
}
.thumbright {
  @include thumb();
  float: right;
  margin: 0px 0px 5px 5px;
}
.thumbleft {
  @include thumb();
  float: left;
  margin: 0px 5px 5px 0px;
}


